    width: 400px; } }

@media (min-width: 1280px) {
  .section-heading {
    width: 263px; } }

@media (min-width: 1280px) {
  .section-heading .title {
    width: 263px; } }

.sale {
  position: relative;
  z-index: 1000;
  padding-bottom: 50px; }
  @media (min-width: 1280px) {
    .sale {
      padding-bottom: 94px; } }
  .sale__gradient {
    display: block;
    height: 61px;
    width: 100%;
    background: linear-gradient(rgba(255, 255, 255, 0), rgba(71, 9, 62, 0.5));
    position: absolute;
    bottom: 0;
    left: 0; }

@media (min-width: 1024px) {
  .sale__content {
    display: flex; } }

.sale__links {
  display: flex;
  flex-direction: column; }
  @media (min-width: 1024px) {
    .sale__links {
      flex-direction: row; } }

@media (min-width: 1440px) {
  .sale__heading {
    padding-top: 69px;
    margin-right: 30px; } }

@media (min-width: 1440px) {
  .sale__heading .sale__title {
    margin-bottom: 30px; } }

@media (min-width: 1440px) {
  .sale__wrapper {
    width: 847px; } }

@media (min-width: 1440px) {
  .sale__wrapper .sale__links {
    margin-top: -10px;
    justify-content: flex-end; } }

@media (min-width: 1440px) {
  .sale__wrapper .sale__links .sale__link:not(:first-child) {
    margin-left: 30px; } }

.sale__wrapper .sale__links .sale__link {
  margin: 0 auto;
  margin-bottom: 20px; }
  @media (min-width: 1024px) {
    .sale__wrapper .sale__links .sale__link {
      margin: 0; } }

.menu {
  position: absolute;
  width: 100%;
  background-color: #ffffff;
  z-index: 2000;
  transition: all 0.3s ease;
  display: none;
  padding-top: 50px; }
  .menu.show {
    display: block; }
  @media (min-width: 1024px) {
    .menu {
      height: 650px;
      padding-top: 75px;
      padding-bottom: 100px; } }
  @media (min-width: 1280px) {
    .menu {
      padding-top: 100px;
      height: 800px; } }
  @media (min-width: 1440px) {
    .menu {
      height: 914px;
      padding-top: 91px; } }
  .menu__links {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    z-index: 1000;
    padding: 0 16px; }
    @media (min-width: 768px) {
      .menu__links {
        padding: 0 24px; } }
    @media (min-width: 1024px) {
      .menu__links {
        padding: 0 32px; } }
    @media (min-width: 1280px) {
      .menu__links {
        padding: 0 100px; } }
    @media (min-width: 1440px) {
      .menu__links {
        padding: 0 150px;
        margin: 0 auto; } }
  .menu__link {
    transition: all 0.3s ease;
    padding: 30px 0;
    font-family: "merriweather", "Arial";
    font-size: 16px;
    line-height: 16px;
    font-weight: 700;
    color: #000000; }
    .menu__link:hover {
      color: #771373;
      opacity: 1; }
    @media (min-width: 1440px) {
      .menu__link {
        padding: 46px 0; } }
  .menu__bg-lines {
    width: 100%;
    z-index: 100;
    position: absolute; }
    .menu__bg-lines svg,
    .menu__bg-lines img {
      fill: none;
      width: 100vw; }
  .menu__bg-img--2 {
    position: absolute;
    top: 30px; }
    @media (min-width: 768px) {
      .menu__bg-img--2 {
        top: -35px; } }
    @media (min-width: 1024px) {
      .menu__bg-img--2 {
        top: -70px; } }
    @media (min-width: 1280px) {
      .menu__bg-img--2 {
        top: -106px; } }
    @media (min-width: 1440px) {
      .menu__bg-img--2 {
        top: -106px; } }
  .menu__bg-img--3 {
    position: absolute;
    top: 160px; }
    @media (min-width: 1280px) {
      .menu__bg-img--3 {
        top: 162px; } }
    @media (min-width: 1440px) {
      .menu__bg-img--3 {
        top: 210px; } }
  .menu__bg-img--4 {
    position: absolute;
    top: 219px; }
    @media (min-width: 768px) {
      .menu__bg-img--4 {
        top: 170px; } }
    @media (min-width: 1280px) {
      .menu__bg-img--4 {
        top: 133px; } }
    @media (min-width: 1440px) {
      .menu__bg-img--4 {
        top: 220px; } }
  .menu__bg-img--5 {
    position: absolute;
    top: 300px; }
    @media (min-width: 1440px) {
      .menu__bg-img--5 {
        top: 417px; } }
  .menu__bg-img--6 {
    position: absolute;
    top: 370px; }
    @media (min-width: 1024px) {
      .menu__bg-img--6 {
        top: 345px; } }
    @media (min-width: 1440px) {
      .menu__bg-img--6 {
        top: 485px; } }

